"use client";

import Link from "next/link";
import { useRouter } from "next/navigation";
import { useState } from "react";

export default function HomePage() {
  const router = useRouter();

  const [videoFile, setVideoFile] = useState<File | null>(null);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0] || null;
    setVideoFile(file);
    setError(null);
  };

  const handleAnalyze = () => {
    if (!videoFile) {
      setError("Please choose a swing video first.");
      return;
    }

    // For now, the AI coach text flow is the working brain.
    // We treat upload as the entry point and send them straight to /coach.
    setIsSubmitting(true);
    router.push("/coach");
  };

  return (
    <main className="min-h-screen bg-slate-950 text-slate-50">
      {/* Top nav */}
      <header className="border-b border-slate-800 bg-slate-950/90 backdrop-blur">
        <div className="max-w-5xl mx-auto flex items-center justify-between px-4 py-3">
          <div className="flex items-center gap-2">
            <div className="h-7 w-7 rounded-full bg-emerald-500 flex items-center justify-center text-slate-950 text-sm font-bold">
              V
            </div>
            <div className="flex flex-col leading-tight">
              <span className="text-sm font-semibold">Virtual Coach AI</span>
              <span className="text-[11px] text-slate-400">
                Swing reports in under a minute
              </span>
            </div>
          </div>

          <nav className="flex items-center gap-3 text-xs">
            <span className="text-emerald-400 font-semibold">Player</span>
            <Link
              href="/coach"
              className="text-slate-300 hover:text-emerald-400"
            >
              Coach
            </Link>
          </nav>
        </div>
      </header>

      {/* Main content */}
      <main className="max-w-5xl mx-auto p-4 py-10">
        <div className="space-y-6">
          <header className="space-y-2">
            <h1 className="text-3xl md:text-4xl font-semibold">
              Virtual Coach AI
            </h1>
            <p className="text-slate-300">
              Upload one swing. Get a tour-style breakdown and a simple practice
              plan.
            </p>
          </header>

          <section className="bg-slate-900/70 border border-slate-800 rounded-2xl p-4 md:p-6 flex flex-col md:flex-row gap-4 md:gap-6">
            <div className="flex-1 space-y-3">
              <h2 className="text-xl font-medium">1. Upload your swing</h2>
              <p className="text-sm text-slate-300">
                Face-on or down-the-line, driver or iron. Just make sure the
                ball and you are fully in frame.
              </p>

              <input
                type="file"
                accept="video/*"
                onChange={handleFileChange}
                className="block w-full text-sm text-slate-200
                           file:mr-4 file:py-2 file:px-4
                           file:rounded-full file:border-0
                           file:text-sm file:font-semibold
                           file:bg-emerald-500 file:text-slate-950
                           hover:file:bg-emerald-400"
              />
              {videoFile && (
                <p className="text-xs text-slate-400 mt-1">
                  Selected: {videoFile.name}
                </p>
              )}

              <div className="mt-3 flex flex-wrap gap-2">
                <button
                  type="button"
                  onClick={handleAnalyze}
                  disabled={isSubmitting}
                  className="inline-flex items-center justify-center rounded-full px-5 py-2 text-sm font-semibold
                             bg-emerald-500 text-slate-950 disabled:opacity-50 disabled:cursor-not-allowed
                             hover:bg-emerald-400 transition"
                >
                  {isSubmitting ? "Sending to coach…" : "Analyze my swing"}
                </button>

                <Link
                  href="/sample-report"
                  className="inline-flex items-center justify-center rounded-full px-5 py-2 text-sm font-semibold
                             border border-slate-600 text-slate-100 hover:bg-slate-800 transition"
                >
                  See a sample report
                </Link>
              </div>

              {error && (
                <p className="text-xs text-red-400 mt-2">
                  {error}
                </p>
              )}
            </div>

            <div className="flex-1 bg-slate-950/60 border border-slate-800 rounded-2xl p-4 text-sm space-y-2">
              <h3 className="font-medium text-slate-100">What you’ll get</h3>
              <ul className="list-disc list-inside text-slate-300 space-y-1">
                <li>Overall swing score (0–100)</li>
                <li>Your top 2–3 priority issues</li>
                <li>Simple drills for each issue</li>
                <li>A 14-day practice focus</li>
              </ul>
            </div>
          </section>
        </div>
      </main>
    </main>
  );
}

